@import url(https://fonts.googleapis.com/css?family=Roboto);
@font-face {
	font-family: 'norwester';
	src: url(/vanilla/static/media/norwester.9d0967bf.ttf); 
	src: 
		local('norwester'),
		url(/vanilla/static/media/norwester.9d0967bf.ttf) 
		format('truetype');
}

html { font-size: calc(1em + 1vw) }

/* Palette generated by Material Palette - materialpalette.com/cyan/amber */
.dark-color            { background: #212121; }
.dark-primary-color    { background: #0097A7; }
.default-primary-color { background: #00BCD4; }
.light-primary-color   { background: #B2EBF2; }
.text-primary-color    { color: #FFFFFF; }
.accent-color          { background: #FFC107; }
.accent-text-color     { color: #FFC107; }
.primary-text-color    { color: #212121; }
.secondary-text-color  { color: #757575; }
.divider-color         { border-color: #BDBDBD; }

/* org create-react-app fonts  
  -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif
  */
body {
  margin: 0;
  font-family: -apple-system,BlinkMacSystemFont,"Roboto","Segoe UI","Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


p {font-size: 0.5em;}
/***********************************************************************
*  Nav Bar
**********************************************************************/
@media only screen and (max-width: 600px) {
  .main-nav {
      font-size: 0.5em;
      padding: 0;
  }
}

.push {
  margin-left: auto;
}

li {
  padding: 20px;
}

a {line-height:1em;
  vertical-align: bottom;
  color: #f5f5f6;
  text-decoration: none; 
}

a:hover {
  color: #FFC107;
  text-decoration: none;
}

.main-nav {
  display: flex;
  list-style: none;
  margin: 0;
  font-size: 0.6em;
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}

.logoLink {
  font-family: 'norwester', sans-serif;
  /*font-size: 0.9em;*/
  font-weight: bold;
}
  
.tipsLink {
  display: contents;
  color: white;
}

.fourLink {
  font-size: 1.8em;
  display: contents;
  font-weight: normal;
  letter-spacing: -2pt;
}


.townLink {
  display: contents;
}


/***********************************************************************
*  Cover
**********************************************************************/
.container {
  /*show heigh: auto  
  align-items: center;
  justify-content: center;
  height: 70vh; 
  */
  height: auto; 
  display: flex;
  background-image: url(/vanilla/static/media/town.90e040a0.jpeg);
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-size: cover;
  color: white;
}

/* image 
.cover {
  width: 100%;
}
*/

.logo-header {
  /* position: absolute; 
  width: 100%;
  top: 100px;
  float: left;
  justify-content: center;
  padding-left: 30%;  
  */
  width: 100vw;
  background: rgb(0, 0, 0); /* fallback color */
  background: rgba(0, 0, 0, 0.3);
  text-align: center;
  padding-top: 100px;
  padding-bottom: 20%;
  font-family: 'norwester', sans-serif;
  font-size: 3em;
  font-weight: bold;
}

.tipsLogo {
  display: contents;
}

.fourLogo {
  display: contents;
  font-size:2em;
  font-weight: normal;
  letter-spacing: -10pt;
}


.townLogo {
  display: contents;
}


/***********************************************************************
*  Headline
**********************************************************************/
#headline {
  height: auto; 
  text-align: center;
  padding-top: 40px;
  /*
  display: flex;
  align-items: center;
  justify-content: center;
  */
}

.headline-header h6 {
  font-weight: normal;
}
